<template>
	<div class="game">
		<div class="start" v-show="gamewrap">
			<div>拼运游戏</div>
			<p @click="ready()">开始游戏</p>
			您上次到达了游戏的第{{count}}关
		</div>
		<div v-show="start" class="gameWrap">
			<div class="player">
				<div class="msg">
					<span><img src="../assets/images/2.jpg"></span>
					<span>Player</span>
				</div>
				<div class="hpanimate" :style="{width: playerHealth + '%'}"></div>
				<div v-show="this.playerHealth > 0" class="hp" :class="{active:animate}" :style="{width: playerHealth + '%'}">
					<p >{{playerHealth}}</p>
				</div>
				<p class="winfail" v-show="this.playerHealth <= 0">{{playerfail}}</p>
			</div>
			<div class="monster">
				<div class="msg">
					<span><img src="../assets/images/1.jpg"></span>
					<span>Monster</span>
				</div>
				<div class="hpanimate" :style="{width: monsterHealth + '%'}"></div>
				<div v-show="this.monsterHealth> 0"class="hp" :class="{active:animate}" :style="{width: monsterHealth + '%'}">
					<p >{{monsterHealth}}</p>
				</div>
				<p class="winfail" v-show="this.monsterHealth <= 0">{{monsterfail}}</p>
			</div>
			<div class="log">
				<ul>
					<li v-for="item in fightlog" :class="{activep:item.isPlayer,activem:
						!item.isPlayer}">
					 {{item.text}}
					</li>
				</ul>
			</div>
			<div class="item" v-if="win == 'a'">
				<div class="button">
					<button @click="attack()">普通攻击</button>
				</div>
				<div class="button">
					<button @click="specialAttack()">技能攻击</button>
				</div>
				<div class="button" @click="huifu()">
					<button>生命恢复</button>
				</div>
				<div class="button">
					<button @click="taopao()">逃跑</button>
				</div>
			</div>
			<div class="item" v-else-if="win == 'b'">
				<div class="button">
					<button @click="go()">下一关</button>
				</div>
				<div class="button">
					<button @click="taopao()">逃跑</button>
				</div>
			</div>
			<div class="item" v-else>
				<div class="button">
					<button @click="taopao()">结束了</button>
				</div>
			</div>
			<div class="jilu">
				第{{count}}关
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'game',
		data(){
			return{
				gamewrap:true,
				start:false,
				playerHealth: 100,
            	monsterHealth: 100,
            	pattack:5,
            	mattack:12,
            	playerfail:'你失败了',
            	monsterfail:'击杀',
            	animate:false,
            	win:'a',
            	fightlog:[],
            	count:1,
            	a:localStorage.getItem('key'),
            	b:1
			}
		},
		methods:{
			ready(){
				this.gamewrap = false
				this.start = true
				this.playerHealth = 100
            	this.monsterHealth = 100
            	this.fightlog = []
            	this.count = 1
            	this.win = 'a'
            	
            	this.pattack = 5
            	this.mattack = 12
			},
			calculateDanage(min, max){
                return Math.max(Math.floor(Math.random() * max) + 1, min)
            },
            playerAttacks(a,b){
            	let damage = this.calculateDanage(a, b);
                this.monsterHealth -= damage;
                this.fightlog.unshift({
                    isPlayer: true,
                    text: 'player的攻击对怪兽造成了' + damage + '点伤害'
                }); 
            },
            monsterAttacks(a,b){
                let damage = this.calculateDanage(a, b);
                this.playerHealth -= damage;
                this.fightlog.unshift({
                    isPlayer: false,
                    text: 'monster的攻击对玩家造成了' + damage + '点伤害'
                }); 
            },
			attack(){
				this.animate = false
				this.playerAttacks(this.pattack, this.mattack)
                this.monsterAttacks(this.pattack,this.mattack)
                this.winorfail()
			},
			specialAttack(){
				this.animate = false
				this.playerAttacks(this.pattack+10, this.mattack+18)
                this.monsterAttacks(this.pattack+10, this.mattack+18)
                this.winorfail()    
			},
			winorfail(){
				if(this.playerHealth <= 0 && this.monsterHealth <= 0) {
             		this.win = 'v'
             	}else if(this.playerHealth <= 0){
             		this.win = 'v'
             	}else if(this.monsterHealth <= 0){
             		this.win = 'b'
             	}
			},
			huifu(){
				this.animate = true
				if (this.playerHealth <= 80) {
                    this.playerHealth += 20
                }else{
                    this.playerHealth = 100
                }
                this.fightlog.unshift({
                    isPlayer: true,
                    text: '玩家治疗了20点血'
                }); 
                if (this.monsterHealth <= 80) {
                    this.monsterHealth += 25
                }else{
                    this.monsterHealth = 100
                }
                this.fightlog.unshift({
                    isPlayer: false,
                    text: '怪兽治疗了25点血'
                }); 
			},
			go(){
				this.playerHealth = 100
            	this.monsterHealth = 100
            	this.win = 'a'
            	this.pattack += 2
            	this.mattack += 2
            	this.count ++      	
			},
			taopao(){
				this.gamewrap = true
				this.start = false
			},
		}
	}
</script>

<style lang="scss" scoped>
	.game{
		.start{
			width: 100%;
			height: 100vh;
			text-align: center;
			overflow: hidden;
			div{
				margin-top: 2.4rem;
				font-size: 0.72rem;
			}
			p{
				margin: 1rem auto;
				width: 2.4rem;
				height: 0.8rem;
				line-height: 0.8rem;
				font-size: 0.4rem;
				display: block;
				background: green;
				color: white;
			}
		}
		.gameWrap{
			padding: 10% 4%;
			padding-top:15%;
			box-sizing: border-box;
			position:relative;
			.monster,.player{
				width: 100%;
				height: 1.6rem;
				margin-bottom: 1rem;
				.msg{
					height: .8rem;
					line-height: .8rem;
					span{
						margin-right: .2rem;
						img{
							width: .5rem;
							height: .5rem;
						}
					}
				}
				.hp{
					width: 100%;
					height: .8rem;
					background:green;
					margin-top: -.8rem;
					display: flex;
					justify-content: center;
					align-items: center;
					color: white;
					position:relative;
				}
				.hpanimate{
					width: 100%;
					height: .8rem;
					transition: .5s;
					background:#f03;
				}
				.winfail{
					width: 100%;
					height: .8rem;
					line-height: .8rem;
					text-align: center;
					margin-top: -.8rem;
					z-index: 3;
					color: white;
					background:#f03;
					font-weight: 700;
					font-size: .4rem;
				}
				.active{
					transition: .5s;
				}
			}
			.log{
				width: 100%;
				height: 3.2rem;
				margin-bottom: 1rem;
				overflow: auto;
				padding:.3rem 0;
				box-sizing: border-box;
				ul{
					li{
						font-size: .26rem;
						margin: .1rem 0;
						&.activep{
							color: rgba(0,0,0,.8);
						}
						&.activem{
							color: rgba(0,0,0,.6);
						}
					}
				}
			}
			.item{
				display: flex;
				flex: 1;
				height: 1rem;
				align-items:center;
				.button{
					width: 25%;
					text-align: center;
					button{
						width: 1.4rem;
						height: .7rem;
						color: white;
						background:green;
					}
				}
			}
			.jilu{
				position:absolute;
				top: .3rem;
				font-size: .28rem;
			}
		}
	}
</style>